import { Mixpanel, options as mixpanelOptions } from './mixpanel';

var namespace = AutodeskNamespace('Autodesk.Viewing');
namespace["MixpanelProvider"] = new Mixpanel(mixpanelOptions);

class MixpanelExtension extends Autodesk.Viewing.Extension {

    constructor(viewer, options) {
        super(viewer, options);
    }

    load() { return true; }
    unload() { return true; }
    activate() { return true; }
    deactivate() { return false; }
}

Autodesk.Viewing.theExtensionManager.registerExtension('Autodesk.Viewing.MixpanelExtension', MixpanelExtension);
